body {
  margin: 0;
  padding: 0;
  font-family: 'Work Sans', sans-serif;
  background: #f4f4ef;
}

#root{
   display: flex;

}
.incdec{
  text-align: center;
  width:100%;
}
.center {
  text-align: center;
}

a {
  color: #636363;
  text-decoration: none;
  transition: all 0.5s ease-in;
}


a:hover {
  text-decoration: underline;
}


/* Navigation */


.navbar {
  background: #fff;
  text-align: center;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 5px 15px 0 rgba(46, 61, 73, 0.12);
}


.navbar h2 {
  margin: 0;
  font-weight: 200;
  font-size: 30px;
  letter-spacing: 4px;
  font-family: 'Work Sans', sans-serif;
}


/* Post Styles */
.post-container {
  background: #fff;
  padding: 50px;
  width: 500px;
  margin: 40px;
  box-shadow: 0 5px 15px 0 rgba(46, 61, 73, 0.12);
  font-family: 'Work Sans', sans-serif;
}


.post_heading {
  text-align: center;
  font-weight: 400;
  font-size: 40px;
  color: #636363;
  margin: 10px;
}


.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}


.form input {
  height: 44px;
  padding-left: 15px;
  padding-right: 15px;
  border: 1px solid #dbe2e8;
  font-size: 14px;
  border-radius: 2px;
  color: #636363;
  box-shadow: 0 2px 2px 0 rgba(46, 60, 73, 0.05);
  outline: none;
  width: 80%;
}


.form textarea {
  width: 80%;
  max-width: 80%;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 15px;
  border: 1px solid #dbe2e8;
  font-size: 14px;
  border-radius: 2px;
  color: #636363;
  font-family: 'Work Sans,', sans-serif;
  box-shadow: 0 2px 2px 0 rgba(46, 60, 73, 0.05);
  outline: none;
}


/* All Posts */


.all_post_heading {
  text-align: center;
  font-weight: 400;
  font-size: 40px;
  color: #636363;
  word-wrap: break-word;
}


.post {
  background: #fff;
  width: 400px;
  margin: 10px 50px;
  padding: 10px;
  box-shadow: 10px 10px 12px 0 rgba(46, 60, 73, 0.05);
}
.addingTaskTable{
  margin: 0 0 10px 0;
  border-spacing:0; 
  width:100%;
  border:1px;  
  border-style: solid;
  border-color: black;
  border-radius: 5px;
  text-align: left;
}
th{
  border-width:0 1px 0 0;  
  border-style: solid;
  border-color: black;
  height:40px;
  padding-left:5px;
}
button{
  cursor: pointer;
}
.tableRows{
  border-width:1px 1px 0 0;  
  padding:5px;
  height:40px;
}
td{
  border-width:1px 0 0 0;  
  border-style: solid;
  border-color: black;
}
.form button {
  background: #02b3e4;
  padding: 15px 10px;
  width: 105px;
  text-align: center;
  margin: 5px;
  color: #fff;
  font-family: 'Work Sans', sans-serif;
  border-radius: 4px;
  border: none;
  font-size: 20px;
}
.userbtn{
  background: #02b3e4;
  padding: 15px 10px;
  width: 105px;
  text-align: center;
  margin: 5px;
  color: #fff;
  font-family: 'Work Sans', sans-serif;
  border-radius: 4px;
  border: none;
  font-size: 20px;
}
#new{
  color:red;
  font-size: 50px;
}
.btn{
  background: #02b3e4;
  padding: 15px 10px;
  width: 150px;
  text-align: center;
  margin: 5px;
  color: #fff;
  font-family: 'Work Sans', sans-serif;
  border-radius: 4px;
  border: none;
  font-size: 20px;
}
.backButton {
  background: #02b3e4;
  padding: 15px 10px;
  width: 105px;
  text-align: center;
  margin: 5px;
  color: #fff;
  font-family: 'Work Sans', sans-serif;
  border-radius: 4px;
  border: none;
  font-size: 20px;
}

.post_title {
  text-align: center;
  font-weight: 400;
  font-size: 40px;
  color: #636363;
  margin: 5px;
  /* word-wrap: break-word; */
}


.post_message {
  font-size: 25px;
  font-weight: 200;
  line-height: 2.5rem;
  text-align: center;
  /* word-wrap: break-word */
